<template>
  <AppRow only-owner>
    <v-col
      cols="12"
      class="text-center text-h5"
    >
      Лог действий
    </v-col>

    <v-col cols="12">
      <v-data-iterator
        :items="logs"
        :items-per-page="5"
        :loading="false"
        no-data-text="Логи отсутствуют"
        loading-text="Загрузка..."
      >
        <template v-slot:default="props">
          <v-expansion-panels
            flat
            focusable
          >
            <v-expansion-panel
              v-for="item of props.items"
              :key="item.id"
            >
              <v-expansion-panel-header>
                <v-row no-gutters>
                  <v-col
                    cols="12"
                    sm="4"
                  >
                    {{ item.data.actionType }}
                  </v-col>
                  <v-col
                    cols="12"
                    sm="8"
                    class="text--secondary"
                  >
                    <v-row no-gutters>
                      <v-col
                        cols="12"
                        sm="4"
                      >
                        {{ item.data.adminUsername }}
                      </v-col>
                      <v-col
                        cols="12"
                        sm="4"
                      >
                        {{ item.data.adminIp }}
                      </v-col>
                      <v-col
                        cols="12"
                        sm="4"
                      >
                        {{ item.data.data.time | moment('DD.MM.YYYY, HH:mm') }}
                      </v-col>
                    </v-row>
                  </v-col>
                </v-row>
              </v-expansion-panel-header>
              <v-expansion-panel-content>
                <v-list dense>
                  <v-list-item
                    v-for="(value, key, index) of item.data.data"
                    :key="index"
                  >
                    <v-list-item-content class="d-inline-block text-truncate">
                      {{ key }}:
                    </v-list-item-content>
                    <v-list-item-content
                      v-if="key === 'time'"
                      class="d-inline-block text-truncate"
                    >
                      {{ value | moment('DD.MM.YYYY, HH:mm') }}
                    </v-list-item-content>
                    <v-list-item-content
                      v-else
                      class="d-inline-block text-truncate"
                    >
                      {{ value }}
                    </v-list-item-content>
                  </v-list-item>
                </v-list>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
        </template>
      </v-data-iterator>
    </v-col>

    <v-col
      cols="12"
      class="text-center text-h5"
    >
      Админы
    </v-col>

    <v-col cols="12">
      <ApolloQuery :query="require('../graphql/queries/getAllAdminUsers.gql')">
        <template v-slot="{ result: { error: adminsError, data: adminsData }, isLoading: adminsIsLoading }">
          <!-- Error -->
          <AppNotification
            v-if="adminsError"
            :message="adminsError.message"
          />

          <!-- Result -->
          <v-card
            v-else-if="adminsData || adminsIsLoading === 1"
            :loading="adminsIsLoading === 1"
            flat
            class="px-4 pb-2"
          >
            <template v-if="adminsData">
              <v-card-title>
                <v-row>
                  <v-col
                    cols="12"
                    sm="6"
                  >
                    Админы:
                  </v-col>
                  <v-col class="text-right">
                    <ApolloMutation
                      :mutation="require(editedIndex === -1 ? '../graphql/mutations/createAdminUser.gql' : '../graphql/mutations/updateAdminUser.gql')"
                      :variables="{
                        id: editedAdmin.id,
                        username: editedAdmin.username,
                        password: editedAdmin.password,
                        role: editedAdmin.role,
                        permissions: editedAdmin.permissions,
                      }"
                      :update="updateCache"
                    >
                      <template v-slot="{ mutate, loading, error: createOrEditError }">
                        <v-dialog
                          v-model="dialog"
                          persistent
                          max-width="500px"
                        >
                          <template v-slot:activator="{ on, attrs }">
                            <v-btn
                              depressed
                              color="primary"
                              class="text-none"
                              large
                              height="36"
                              v-bind="attrs"
                              v-on="on"
                            >
                              Создать
                            </v-btn>
                          </template>

                          <v-card :loading="loading">
                            <v-card-title>
                              <span class="text-h5">{{ formTitle }}</span>
                            </v-card-title>

                            <v-card-text>
                              <v-container>
                                <v-row>
                                  <v-col
                                    cols="12"
                                    sm="6"
                                  >
                                    <v-text-field
                                      v-model="editedAdmin.username"
                                      hide-details="auto"
                                      label="Имя"
                                    />
                                  </v-col>
                                  <v-col
                                    cols="12"
                                    sm="6"
                                  >
                                    <v-text-field
                                      v-model="editedAdmin.password"
                                      :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
                                      :type="show ? 'text' : 'password'"
                                      hide-details="auto"
                                      label="Пароль"
                                      @click:append="show = !show"
                                    />
                                  </v-col>
                                  <v-col cols="12">
                                    <v-select
                                      v-model="editedAdmin.role"
                                      :items="roles"
                                      hide-details="auto"
                                      clearable
                                      label="Роль"
                                    />
                                  </v-col>
                                  <v-col cols="12">
                                    <v-select
                                      v-model="editedAdmin.permissions"
                                      :items="permissionsItems"
                                      hide-details="auto"
                                      clearable
                                      label="Права"
                                      multiple
                                    >
                                      <template v-slot:selection="{ item, index }">
                                        <v-chip v-if="index === 0">
                                          <span>{{ item }}</span>
                                        </v-chip>
                                        <span
                                          v-if="index === 1"
                                          class="grey--text text-caption"
                                        >
                                          (+{{ editedAdmin.permissions.length - 1 }} others)
                                        </span>
                                      </template>
                                    </v-select>
                                  </v-col>
                                </v-row>
                              </v-container>
                            </v-card-text>

                            <v-card-actions>
                              <v-spacer />

                              <v-btn
                                text
                                color="primary"
                                @click="close"
                              >
                                Отмена
                              </v-btn>
                              <v-btn
                                text
                                color="primary"
                                @click="mutate()"
                              >
                                {{ buttonText }}
                              </v-btn>
                            </v-card-actions>
                          </v-card>
                        </v-dialog>
                        <AppNotification
                          v-if="createOrEditError"
                          :message="createOrEditError.message"
                          :icon-disabled="true"
                        />
                      </template>
                    </ApolloMutation>
                  </v-col>
                </v-row>
              </v-card-title>
              <v-data-table
                :headers="headers"
                :items="adminsData.getAllAdminUsers"
                :items-per-page="5"
                no-data-text="Админы отсутствуют"
              >
                <template v-slot:[`item.permissions`]="{ item }">
                  <v-chip v-if="item.permissions[0]">
                    <span>{{ item.permissions[0] }}</span>
                  </v-chip>
                  <span v-else>
                    {{ item.role === 'SUPERUSER' ? 'Все' : 'Отсутствуют' }}
                  </span>
                  <span
                    v-if="item.permissions[1]"
                    class="grey--text text-caption"
                  >
                    (+{{ item.permissions.length - 1 }} others)
                  </span>
                </template>

                <template v-slot:[`item.createdAt`]="{ item }">
                  {{ item.createdAt | moment('DD.MM.YYYY, HH:mm') }}
                </template>

                <template v-slot:[`item.action`]="{ item }">
                  <v-btn
                    depressed
                    class="text-none"
                    large
                    height="36"
                    @click="editAdmin(item)"
                  >
                    Редактировать
                  </v-btn>
                </template>
              </v-data-table>
            </template>
          </v-card>

          <!-- No result -->
          <AppNotification
            v-else
            message="Нет результата :("
          />
        </template>
      </ApolloQuery>
    </v-col>

    <v-col
      cols="12"
      class="text-center text-h5"
    >
      Чат
    </v-col>

    <v-col
      cols="12"
      sm="6"
    >
      <ApolloMutation
        :mutation="require('../graphql/mutations/setChatAsDisabled.gql')"
        :variables="{
          disable: true,
        }"
      >
        <template v-slot="{ mutate, loading, error: disableError }">
          <v-btn
            :loading="loading"
            block
            depressed
            class="text-none rounded-lg"
            x-large
            @click="mutate()"
          >
            Выключить чат
          </v-btn>
          <AppNotification
            v-if="disableError"
            :message="disableError.message"
            :icon-disabled="true"
          />
        </template>
      </ApolloMutation>
    </v-col>

    <v-col
      cols="12"
      sm="6"
    >
      <ApolloMutation
        :mutation="require('../graphql/mutations/setChatAsDisabled.gql')"
        :variables="{
          disable: false,
        }"
      >
        <template v-slot="{ mutate, loading, error: enableError }">
          <v-btn
            :loading="loading"
            block
            depressed
            color="primary"
            class="text-none rounded-lg"
            x-large
            @click="mutate()"
          >
            Включить чат
          </v-btn>
          <AppNotification
            v-if="enableError"
            :message="enableError.message"
            :icon-disabled="true"
          />
        </template>
      </ApolloMutation>
    </v-col>

    <v-col
      cols="12"
      class="text-center text-h5"
    >
      Игровые режимы
    </v-col>

    <v-col>
      <ApolloQuery :query="require('../graphql/queries/getActiveGames.gql')">
        <template v-slot="{ result: { error: gamesError, data: gamesData }, isLoading: gamesIsLoading }">
          <!-- Error -->
          <AppNotification
            v-if="gamesError"
            :message="gamesError.message"
          />

          <!-- Result -->
          <v-card
            v-else-if="gamesData || gamesIsLoading === 1"
            :loading="gamesIsLoading === 1"
            flat
            class="px-4 pb-2"
          >
            <template v-if="gamesData">
              <v-data-table
                :headers="headersGames"
                :items="gamesData.getActiveGames"
                :items-per-page="5"
                no-data-text="Режимы отсутствуют"
              >
                <template v-slot:[`item.createdAt`]="{ item }">
                  {{ item.createdAt | moment('DD.MM.YYYY, HH:mm') }}
                </template>

                <template v-slot:[`item.active`]="{ item }">
                  <v-simple-checkbox
                    v-model="item.active"
                    disabled
                  />
                </template>

                <template v-slot:[`item.action`]="{ item }">
                  <v-btn
                    depressed
                    :color="item.active ? '' : 'primary'"
                    class="text-none"
                    large
                    height="36"
                  >
                    {{ item.active ? 'Выключить' : 'Включить' }}
                  </v-btn>
                </template>
              </v-data-table>
            </template>
          </v-card>

          <!-- No result -->
          <AppNotification
            v-else
            message="Нет результата :("
          />
        </template>
      </ApolloQuery>
    </v-col>
  </AppRow>
</template>

<script>
import AppRow from '@/components/AppRow.vue';
import AppNotification from '@/components/AppNotification.vue';
import adminsQuery from '@/graphql/queries/getAllAdminUsers.gql';

export default {
  components: {
    AppRow,
    AppNotification,
  },
  data() {
    return {
      logs: [
        {
          data: {
            actionType: 'Редактирование пользователя',
            adminUsername: 'Admin',
            adminIp: '192.168.1.3',
            data: {
              userId: 445,
              balance: 999,
              time: 1604833422,
            },
          },
        },
        {
          data: {
            actionType: 'Удаление пользователя',
            adminUsername: 'Test',
            adminIp: '192.168.1.3',
            data: {
              userId: 2,
              time: 1604833100,
            },
          },
        },
      ],
      dialog: false,
      editedIndex: -1,
      editedAdmin: {},
      show: false,
      roles: ['ROLE_UNDEFINED', 'USER', 'ADMINISTRATOR', 'SUPERUSER', 'PARTNER', 'BLOGGER', 'BANNED', 'PUBLIC'],
      permissionsItems: ['DASHBOARD_ADMINISTRATION', 'CHAT_ADMINISTRATION', 'BONUS_ADMINISTRATION', 'USERS_ADMINISTRATION', 'WITHDRAW_ADMINISTRATION', 'GAMES_ADMINISTRATION'],
      headers: [
        { text: 'ID:', value: 'id', class: 'white--text text-subtitle-2' },
        { text: 'Имя:', value: 'username', class: 'white--text text-subtitle-2' },
        { text: 'Роль:', value: 'role', class: 'white--text text-subtitle-2' },
        { text: 'Права:', value: 'permissions', class: 'white--text text-subtitle-2' },
        { text: 'Дата регистрации:', value: 'createdAt', class: 'white--text text-subtitle-2' },
        { text: '', value: 'action', align: 'center' },
      ],
      headersGames: [
        { text: 'Режим:', value: 'gameMode', class: 'white--text text-subtitle-2' },
        { text: 'Дата создания:', value: 'createdAt', class: 'white--text text-subtitle-2' },
        { text: 'Активен:', value: 'active', class: 'white--text text-subtitle-2' },
        { text: '', value: 'action', align: 'center' },
      ],
    };
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? 'Создать админа' : 'Редактирование';
    },
    buttonText() {
      return this.editedIndex === -1 ? 'Создать' : 'Сохранить';
    },
  },
  methods: {
    updateCache(store, { data: { createAdminUser } }) {
      if (this.editedIndex === -1) {
        const query = {
          query: adminsQuery,
        };
        // Read the query from cache
        const data = store.readQuery(query);
        // Mutate cache result
        data.getAllAdminUsers.push(createAdminUser);
        // Write back to the cache
        store.writeQuery({
          ...query,
          data,
        });
      }

      this.close();
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedIndex = -1;
        this.editedAdmin = {};
      });
    },
    editAdmin(item) {
      this.editedIndex = item.id;
      this.editedAdmin = { ...item };
      this.dialog = true;
    },
  },
};
</script>
